import React from 'react';
import { Alert, StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import type { Lang } from './i18n/visaOptimizer';
import { visaOptimizerI18n } from './i18n/visaOptimizer';

const STORAGE_LANG = 'visa_optimizer_lang';

const LANG_OPTIONS: { lang: Lang; label: string }[] = [
  { lang: 'zh', label: '' },
  { lang: 'ja', label: '' },
];

interface LangSwitchScreenProps {
  currentLang: Lang;
  onBack: () => void;
  onConfirm: () => void;
}

export function LangSwitchScreen({ currentLang, onBack, onConfirm }: LangSwitchScreenProps) {
  const handleSelect = (targetLang: Lang) => {
    if (targetLang === currentLang) {
      onBack();
      return;
    }
    const t = visaOptimizerI18n[targetLang];
    Alert.alert('', t.confirmSwitchMessage, [
      { text: t.close, style: 'cancel' },
      {
        text: t.switchToLang,
        onPress: () => {
          AsyncStorage.setItem(STORAGE_LANG, targetLang);
          onConfirm();
          onBack();
        },
      },
    ]);
  };

  const t = visaOptimizerI18n[currentLang];

  return (
    <View style={styles.container}>
      <View style={styles.header}>
        <TouchableOpacity onPress={onBack} style={styles.backBtn} hitSlop={{ top: 12, bottom: 12, left: 12, right: 12 }}>
          <Text style={styles.backText}></Text>
        </TouchableOpacity>
        <Text style={styles.title}>{t.langSwitchTitle}</Text>
        <View style={styles.backBtn} />
      </View>
      <View style={styles.list}>
        {LANG_OPTIONS.map(({ lang, label }) => (
          <TouchableOpacity
            key={lang}
            style={styles.row}
            onPress={() => handleSelect(lang)}
            activeOpacity={0.7}
          >
            <Text style={styles.rowLabel}>{label}</Text>
            {currentLang === lang && <Text style={styles.rowCheck}></Text>}
          </TouchableOpacity>
        ))}
      </View>
    </View>
  );
}

const colors = {
  visaBlue: '#0033a0',
  bg: '#f0f2f5',
  card: '#fff',
  border: '#ddd',
  text: '#333',
};

const styles = StyleSheet.create({
  container: { flex: 1, backgroundColor: colors.bg },
  header: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    paddingHorizontal: 8,
    paddingVertical: 12,
    backgroundColor: colors.card,
    borderBottomWidth: 1,
    borderBottomColor: colors.border,
  },
  backBtn: { minWidth: 44, alignItems: 'flex-start' },
  backText: { fontSize: 28, color: colors.visaBlue, fontWeight: '300' },
  title: { fontSize: 17, fontWeight: '600', color: colors.text },
  list: { marginTop: 12, paddingHorizontal: 10 },
  row: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    backgroundColor: colors.card,
    padding: 16,
    borderRadius: 12,
    marginBottom: 8,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 1 },
    shadowOpacity: 0.05,
    shadowRadius: 4,
    elevation: 2,
  },
  rowLabel: { fontSize: 16, color: colors.text },
  rowCheck: { fontSize: 16, color: colors.visaBlue, fontWeight: '600' },
});
