import React, { useCallback, useEffect, useState } from 'react';
import {
  Alert,
  Dimensions,
  Modal,
  Pressable,
  ScrollView,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import type { Lang } from './i18n/visaOptimizer';
import { visaOptimizerI18n } from './i18n/visaOptimizer';
import { IconTrash } from './components/TabIcons';
import type { Currency, ConvertTo, Rates } from './currency';
import { CURRENCY_SYMBOL, getRate } from './currency';
import type { ListItem, SavedListEntry } from './VisaOptimizer';
import {
  calculateGroupsFromItems,
  DEFAULT_TARGET,
  type GroupResult,
  STORAGE_SAVED_LISTS,
} from './VisaOptimizer';

const STORAGE_LANG = 'visa_optimizer_lang';

function formatDate(iso: string): string {
  const d = new Date(iso);
  return `${d.getFullYear()}/${String(d.getMonth() + 1).padStart(2, '0')}/${String(d.getDate()).padStart(2, '0')} ${String(d.getHours()).padStart(2, '0')}:${String(d.getMinutes()).padStart(2, '0')}`;
}

interface SavedListsScreenProps {
  baseCurrency: Currency;
  convertCurrency: ConvertTo;
  rates: Rates | null;
}

export function SavedListsScreen({
  baseCurrency,
  convertCurrency,
  rates,
}: SavedListsScreenProps) {
  const [savedLists, setSavedLists] = useState<SavedListEntry[]>([]);
  const [lang, setLang] = useState<Lang>('ja');
  const [detailEntry, setDetailEntry] = useState<SavedListEntry | null>(null);

  const symbol = CURRENCY_SYMBOL[baseCurrency];
  const showConvert =
    convertCurrency !== 'none' && convertCurrency !== baseCurrency;
  const convertSymbol = showConvert ? CURRENCY_SYMBOL[convertCurrency] : '';
  const convertRate = showConvert ? getRate(baseCurrency, convertCurrency, rates) : 0;

  const load = useCallback(async () => {
    const [[, listsStr], [, langStr]] = await AsyncStorage.multiGet([
      STORAGE_SAVED_LISTS,
      STORAGE_LANG,
    ]);
    if (langStr === 'zh' || langStr === 'ja') setLang(langStr);
    if (listsStr) {
      try {
        setSavedLists(JSON.parse(listsStr));
      } catch {}
    } else {
      setSavedLists([]);
    }
  }, []);

  useEffect(() => {
    load();
  }, [load]);

  const deleteEntry = useCallback(
    async (entry: SavedListEntry) => {
      const next = savedLists.filter((e) => e.id !== entry.id);
      setSavedLists(next);
      await AsyncStorage.setItem(STORAGE_SAVED_LISTS, JSON.stringify(next));
      setDetailEntry(null);
    },
    [savedLists],
  );

  const confirmDelete = useCallback(
    (entry: SavedListEntry) => {
      const t = visaOptimizerI18n[lang];
      Alert.alert(t.deleteConfirmTitle, t.deleteConfirmMessage, [
        { text: t.saveModalCancel, style: 'cancel' },
        { text: t.deleteSaved, style: 'destructive', onPress: () => deleteEntry(entry) },
      ]);
    },
    [lang, deleteEntry],
  );

  const t = visaOptimizerI18n[lang];

  return (
    <ScrollView
      style={styles.scroll}
      contentContainerStyle={styles.scrollContent}
      keyboardShouldPersistTaps="handled"
    >
      <Text style={styles.sectionTitle}>{t.tabSaved}</Text>
      {savedLists.length === 0 ? (
        <Text style={styles.empty}>{t.noSavedLists}</Text>
      ) : (
        savedLists
          .slice()
          .reverse()
          .map((entry) => (
            <View key={entry.id} style={styles.card}>
              <TouchableOpacity
                style={styles.cardContent}
                onPress={() => setDetailEntry(entry)}
                activeOpacity={0.7}
              >
                <Text style={styles.cardName}>{entry.name}</Text>
                <Text style={styles.cardDate}>{formatDate(entry.date)}</Text>
                <Text style={styles.cardTarget}>
                  {t.savedTargetLabel}:{' '}
                  {(entry.target ?? 0) <= 0
                    ? t.savedTargetNoGroup
                    : `${symbol}${(entry.target ?? 0).toLocaleString()}`}
                </Text>
                <Text style={styles.cardHint}>{t.viewDetail}</Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={styles.cardDelete}
                onPress={() => confirmDelete(entry)}
                hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
              >
                <IconTrash color={colors.danger} size={22} />
              </TouchableOpacity>
            </View>
          ))
      )}

      <Modal
        visible={detailEntry !== null}
        transparent
        animationType="fade"
        onRequestClose={() => setDetailEntry(null)}
      >
        <View style={styles.modalOverlay}>
          <Pressable
            style={StyleSheet.absoluteFill}
            onPress={() => setDetailEntry(null)}
          />
          <View style={styles.modalBox}>
            {detailEntry && (() => {
              const savedTarget = detailEntry.target ?? DEFAULT_TARGET;
              const noGrouping = savedTarget <= 0;
              const groups: GroupResult[] = calculateGroupsFromItems(
                detailEntry.items,
                savedTarget <= 0 ? 0 : savedTarget,
              );
              const total = detailEntry.items.reduce(
                (s, i) => s + i.singleFinalPrice * i.qty,
                0,
              );
              return (
                <>
                  <ScrollView
                    style={styles.modalScroll}
                    contentContainerStyle={styles.modalScrollContent}
                    showsVerticalScrollIndicator={true}
                    nestedScrollEnabled
                    bounces={true}
                  >
                    <Text style={styles.modalTitle}>{detailEntry.name}</Text>
                    <Text style={styles.modalDate}>{formatDate(detailEntry.date)}</Text>
                    <View style={styles.detailTotal}>
                      <Text style={styles.detailTotalLabel}>{t.totalLabel}: </Text>
                      <Text style={styles.detailTotalValue}>
                        {symbol}{total.toLocaleString()}
                        {showConvert &&
                          `  ${convertSymbol} ${Math.round(total * convertRate)}`}
                      </Text>
                    </View>
                    <Text style={styles.detailGroupsTitle}>{t.listTitle}</Text>
                    {noGrouping ? (
                      <View style={styles.detailItemList}>
                        {detailEntry.items.map((i) => (
                          <View key={i.id} style={styles.detailItem}>
                            <Text style={styles.detailItemName}>
                              {i.name || '-'} x{i.qty}
                            </Text>
                            <Text style={styles.detailItemPrice}>
                              {symbol}{(i.singleFinalPrice * i.qty).toLocaleString()}
                            </Text>
                          </View>
                        ))}
                      </View>
                    ) : (
                      groups.map((g, idx) => {
                        const ok = g.sum >= savedTarget;
                        const byName: Record<string, { count: number; unitPrice: number }> = {};
                        g.items.forEach((it) => {
                          const key = it.name ?? '';
                          if (!byName[key]) byName[key] = { count: 0, unitPrice: it.finalPrice };
                          byName[key].count += 1;
                        });
                        const hasEntries = Object.keys(byName).length > 0;
                        return (
                          <View
                            key={idx}
                            style={[
                              styles.resultGroup,
                              ok ? styles.resultGroupOk : styles.resultGroupFail,
                            ]}
                          >
                            <View
                              style={[
                                styles.badge,
                                ok ? styles.badgeOk : styles.badgeFail,
                              ]}
                            >
                              <Text
                                style={[
                                  styles.badgeText,
                                  ok ? styles.badgeTextOk : styles.badgeTextFail,
                                ]}
                              >
                                {ok ? t.qualified : t.unqualified}
                              </Text>
                            </View>
                            <Text style={styles.resultSum}>
                              {t.groupName.replace('{n}', String(idx + 1))}: {symbol}
                              {g.sum.toLocaleString()}
                            </Text>
                            {showConvert && (
                              <Text style={styles.resultTwd}>
                                 {convertSymbol} {Math.round(g.sum * convertRate)}
                              </Text>
                            )}
                            <View style={styles.detailItemList}>
                              {hasEntries
                                ? Object.entries(byName).map(([name, { count, unitPrice }]) => (
                                    <View key={`${idx}-${name}`} style={styles.detailItem}>
                                      <Text style={styles.detailItemName}>
                                        {name || '-'} x{count}
                                      </Text>
                                      <Text style={styles.detailItemPrice}>
                                        {symbol}{(unitPrice * count).toLocaleString()}
                                      </Text>
                                    </View>
                                  ))
                                : g.items.map((it, i) => (
                                    <View key={`${idx}-${it.id}-${i}`} style={styles.detailItem}>
                                      <Text style={styles.detailItemName}>
                                        {it.name || '-'} x1
                                      </Text>
                                      <Text style={styles.detailItemPrice}>
                                        {symbol}{it.finalPrice.toLocaleString()}
                                      </Text>
                                    </View>
                                  ))}
                            </View>
                            {!ok && (
                              <Text style={styles.suggestion}>
                                {t.suggest.replace('{m}', String(savedTarget - g.sum)).replace('', symbol)}
                              </Text>
                            )}
                          </View>
                        );
                      })
                    )}
                  </ScrollView>
                  <View style={styles.modalActions}>
                    <TouchableOpacity
                      style={styles.modalDeleteBtn}
                      onPress={() => confirmDelete(detailEntry)}
                    >
                      <Text style={styles.modalDeleteBtnText}>{t.deleteSaved}</Text>
                    </TouchableOpacity>
                    <TouchableOpacity
                      style={styles.modalCloseBtn}
                      onPress={() => setDetailEntry(null)}
                    >
                      <Text style={styles.modalCloseBtnText}>{t.close}</Text>
                    </TouchableOpacity>
                  </View>
                </>
              );
            })()}
          </View>
        </View>
      </Modal>
    </ScrollView>
  );
}

