/**
 *  / 
 *  API 
 */

export type Currency = 'JPY' | 'TWD' | 'USD';

/**    */
export type ConvertTo = 'none' | Currency;

export const CURRENCIES: Currency[] = ['JPY', 'TWD', 'USD'];

export const CURRENCY_SYMBOL: Record<Currency, string> = {
  JPY: '',
  TWD: 'NT$',
  USD: '$',
};

/**  JPY  (1 JPY = x ) getRate */
export interface Rates {
  JPY_TO_TWD: number;
  JPY_TO_USD: number;
}

/** API  */
export const DEFAULT_RATES: Rates = {
  JPY_TO_TWD: 0.215,
  JPY_TO_USD: 0.0067,
};

const RATES_API_URL = 'https://cdn.moneyconvert.net/api/latest.json';

function getRateFromJPY(to: Currency, rates: Rates): number {
  if (to === 'JPY') return 1;
  if (to === 'TWD') return rates.JPY_TO_TWD;
  return rates.JPY_TO_USD;
}

/**  from -> to  (amount * rate = )rates  null  DEFAULT_RATES */
export function getRate(
  from: Currency,
  to: Currency,
  rates: Rates | null = null,
): number {
  if (from === to) return 1;
  const r = rates ?? DEFAULT_RATES;
  const jpyPerFrom = from === 'JPY' ? 1 : 1 / getRateFromJPY(from, r);
  const toPerJpy = getRateFromJPY(to, r);
  return jpyPerFrom * toPerJpy;
}

/**  API base USD JPY  */
export async function fetchRates(): Promise<Rates> {
  const res = await fetch(RATES_API_URL);
  if (!res.ok) throw new Error(`Rates API ${res.status}`);
  const data = (await res.json()) as { rates?: { JPY?: number; TWD?: number } };
  const r = data.rates;
  if (!r?.JPY || !r?.TWD || r.JPY <= 0) throw new Error('Invalid rates');
  return {
    JPY_TO_USD: 1 / r.JPY,
    JPY_TO_TWD: r.TWD / r.JPY,
  };
}

export const STORAGE_BASE_CURRENCY = 'visa_optimizer_base_currency';
export const STORAGE_CONVERT_CURRENCY = 'visa_optimizer_convert_currency';
export const STORAGE_RATES = 'visa_optimizer_rates';

const RATES_CACHE_HOURS = 24;

export interface StoredRates {
  JPY_TO_TWD: number;
  JPY_TO_USD: number;
  fetchedAt: string;
}

export function isRatesFresh(fetchedAt: string, maxAgeHours: number = RATES_CACHE_HOURS): boolean {
  const then = new Date(fetchedAt).getTime();
  return Date.now() - then < maxAgeHours * 60 * 60 * 1000;
}

export const DEFAULT_BASE_CURRENCY: Currency = 'JPY';
export const DEFAULT_CONVERT_TO: ConvertTo = 'none';

export function parseBaseCurrency(s: string | null): Currency {
  if (s === 'JPY' || s === 'TWD' || s === 'USD') return s;
  return DEFAULT_BASE_CURRENCY;
}

export function parseConvertTo(s: string | null): ConvertTo {
  if (s === 'none' || s === 'JPY' || s === 'TWD' || s === 'USD') return s;
  return DEFAULT_CONVERT_TO;
}
