import React, { useCallback, useEffect, useState } from 'react';
import {
  Alert,
  Modal,
  ScrollView,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { IconTrash } from './components/TabIcons';
import type { Currency, ConvertTo, Rates } from './currency';
import { CURRENCY_SYMBOL, getRate } from './currency';
import {
  DISCOUNT_OPTIONS,
  type Lang,
  visaOptimizerI18n,
} from './i18n/visaOptimizer';

const STORAGE_ITEMS = 'visa_optimizer_items';
const STORAGE_LANG = 'visa_optimizer_lang';
export const STORAGE_SAVED_LISTS = 'visa_optimizer_saved_lists';

export const DEFAULT_TARGET = 1000;

export interface ListItem {
  id: number;
  name: string;
  price: number;
  qty: number;
  discount: number;
  singleFinalPrice: number;
}

export interface SavedListEntry {
  id: string;
  name: string;
  date: string;
  items: ListItem[];
  /**  ()0  */
  target?: number;
}

interface FlatItem {
  id: string;
  name: string;
  finalPrice: number;
}

export interface GroupResult {
  items: FlatItem[];
  sum: number;
}

export function calculateGroupsFromItems(
  items: ListItem[],
  target: number = DEFAULT_TARGET,
): GroupResult[] {
  const flatPool: FlatItem[] = [];
  items.forEach((i) => {
    for (let n = 0; n < i.qty; n++) {
      flatPool.push({
        id: `${i.id}_${n}`,
        name: i.name,
        finalPrice: i.singleFinalPrice,
      });
    }
  });
  flatPool.sort((a, b) => b.finalPrice - a.finalPrice);
  if (target <= 0) {
    const sum = flatPool.reduce((s, x) => s + x.finalPrice, 0);
    return [{ items: flatPool, sum }];
  }
  let remaining = flatPool;
  const groups: GroupResult[] = [];
  while (true) {
    let bestGroup: GroupResult | null = null;
    function backtrack(start: number, currentGroup: FlatItem[], currentSum: number) {
      if (currentSum >= target) {
        if (!bestGroup || currentSum < bestGroup.sum) {
          bestGroup = { items: [...currentGroup], sum: currentSum };
        }
        return;
      }
      for (let i = start; i < remaining.length; i++) {
        currentGroup.push(remaining[i]);
        backtrack(i + 1, currentGroup, currentSum + remaining[i].finalPrice);
        currentGroup.pop();
      }
    }
    backtrack(0, [], 0);
    if (!bestGroup) break;
    const chosen: GroupResult = bestGroup;
    groups.push(chosen);
    const usedIds = new Set(chosen.items.map((x: FlatItem) => x.id));
    remaining = remaining.filter((x: FlatItem) => !usedIds.has(x.id));
  }
  if (remaining.length > 0) {
    groups.push({
      items: remaining,
      sum: remaining.reduce((s, x) => s + x.finalPrice, 0),
    });
  }
  return groups;
}

function loadSaved(
  setItems: (v: ListItem[]) => void,
  setLang: (l: Lang) => void,
) {
  AsyncStorage.multiGet([STORAGE_ITEMS, STORAGE_LANG]).then(([[, itemsStr], [, langStr]]) => {
    if (itemsStr) {
      try {
        setItems(JSON.parse(itemsStr));
      } catch {}
    }
    if (langStr === 'zh' || langStr === 'ja') setLang(langStr);
  });
}

function saveData(items: ListItem[], lang: Lang) {
  AsyncStorage.setItem(STORAGE_ITEMS, JSON.stringify(items));
  AsyncStorage.setItem(STORAGE_LANG, lang);
}

function parseTarget(s: string): number {
  const n = parseInt(s, 10);
  return Number.isNaN(n) || n < 0 ? 0 : n;
}

interface VisaOptimizerProps {
  baseCurrency: Currency;
  convertCurrency: ConvertTo;
  rates: Rates | null;
}

export function VisaOptimizer({ baseCurrency, convertCurrency, rates }: VisaOptimizerProps) {
  const [items, setItems] = useState<ListItem[]>([]);
  const [currentLang, setCurrentLang] = useState<Lang>('ja');
  const [targetStr, setTargetStr] = useState(String(DEFAULT_TARGET));
  const target = parseTarget(targetStr);
  const [itemName, setItemName] = useState('');
  const [itemPrice, setItemPrice] = useState('');
  const [itemQty, setItemQty] = useState('1');
  const [itemDiscount, setItemDiscount] = useState<number | 'custom'>(1);
  const [customDiscount, setCustomDiscount] = useState('');
  const [results, setResults] = useState<GroupResult[]>([]);
  const [saveModalVisible, setSaveModalVisible] = useState(false);
  const [saveModalName, setSaveModalName] = useState('');

  useEffect(() => {
    loadSaved(setItems, setCurrentLang);
  }, []);

  const t = visaOptimizerI18n[currentLang];
  const total = items.reduce((s, i) => s + i.singleFinalPrice * i.qty, 0);
  const symbol = CURRENCY_SYMBOL[baseCurrency];
  const showConvert =
    convertCurrency !== 'none' && convertCurrency !== baseCurrency;
  const convertSymbol = showConvert ? CURRENCY_SYMBOL[convertCurrency] : '';
  const convertRate = showConvert ? getRate(baseCurrency, convertCurrency, rates) : 0;

  const persist = useCallback(
    (newItems: ListItem[]) => {
      setItems(newItems);
      saveData(newItems, currentLang);
    },
    [currentLang],
  );


  const addItem = useCallback(() => {
    const name = itemName.trim() || (currentLang === 'zh' ? '' : '');
    const price = parseFloat(itemPrice);
    const qty = Math.max(1, parseInt(itemQty, 10) || 1);
    let discount = 1;
    if (itemDiscount === 'custom') {
      const v = parseFloat(customDiscount);
      if (!Number.isNaN(v)) discount = v / 100;
    } else {
      discount = itemDiscount;
    }
    if (!price || price <= 0) return;
    const singleFinalPrice = Math.round(price * discount);
    const newItem: ListItem = {
      id: Date.now(),
      name,
      price,
      qty,
      discount,
      singleFinalPrice,
    };
    const newItems = [...items, newItem];
    persist(newItems);
    setItemName('');
    setItemPrice('');
    setItemQty('1');
    setItemDiscount(1);
    setCustomDiscount('');
  }, [
    itemName,
    itemPrice,
    itemQty,
    itemDiscount,
    customDiscount,
    currentLang,
    items,
    persist,
  ]);

  const deleteItem = useCallback(
    (id: number) => {
      persist(items.filter((i) => i.id !== id));
    },
    [items, persist],
  );

  const editItem = useCallback(
    (id: number) => {
      const i = items.find((x) => x.id === id);
      if (i) {
        setItemName(i.name);
        setItemPrice(String(i.price));
        setItemQty(String(i.qty));
        setItemDiscount(i.discount >= 0 && i.discount <= 1 && i.discount !== 1 ? i.discount : 1);
        setCustomDiscount(i.discount > 0 && i.discount < 1 ? String(Math.round(i.discount * 100)) : '');
        deleteItem(id);
      }
    },
    [items, deleteItem],
  );

  const clearAll = useCallback(() => {
    const msg =
      currentLang === 'zh'
        ? ''
        : '';
    Alert.alert('', msg, [
      { text: currentLang === 'zh' ? '' : '', style: 'cancel' },
      {
        text: currentLang === 'zh' ? '' : 'OK',
        onPress: () => {
          setItems([]);
          setResults([]);
          AsyncStorage.multiRemove([STORAGE_ITEMS]);
        },
      },
    ]);
  }, [currentLang]);

  const saveCurrentList = useCallback(async () => {
    const name = saveModalName.trim() || (currentLang === 'zh' ? '' : '');
    if (items.length === 0) return;
    try {
      const raw = await AsyncStorage.getItem(STORAGE_SAVED_LISTS);
      const lists: SavedListEntry[] = raw ? JSON.parse(raw) : [];
      lists.push({
        id: String(Date.now()),
        name,
        date: new Date().toISOString(),
        items: items.map((i) => ({ ...i })),
        target,
      });
      await AsyncStorage.setItem(STORAGE_SAVED_LISTS, JSON.stringify(lists));
      setSaveModalVisible(false);
      setSaveModalName('');
    } catch {}
  }, [items, saveModalName, currentLang, target]);

  const openSaveModal = useCallback(() => {
    setSaveModalName('');
    setSaveModalVisible(true);
  }, []);

  const calculateGroups = useCallback(() => {
    if (items.length === 0) return;
    setResults(calculateGroupsFromItems(items, target));
  }, [items, target]);

  const showCustomDiscount = itemDiscount === 'custom';

  return (
    <ScrollView
      style={styles.scroll}
      contentContainerStyle={styles.scrollContent}
      keyboardShouldPersistTaps="handled"
    >
      <View style={styles.headerBar}>
        <Text style={styles.title}>{t.title}</Text>
      </View>

      <View style={styles.card}>
        <Text style={styles.targetLabel}>{t.targetLabel}</Text>
        <TextInput
          style={styles.targetInput}
          value={targetStr}
          onChangeText={setTargetStr}
          placeholder={t.targetPlaceholder}
          placeholderTextColor="#999"
          keyboardType="number-pad"
        />
      </View>

      <View style={styles.card}>
        <View style={styles.inputRow}>
          <TextInput
            style={[styles.input, styles.flex2]}
            placeholder={t.placeholderName}
            placeholderTextColor="#999"
            value={itemName}
            onChangeText={setItemName}
          />
          <TextInput
            style={[styles.input, styles.flex1]}
            placeholder={t.placeholderPrice}
            placeholderTextColor="#999"
            value={itemPrice}
            onChangeText={setItemPrice}
            keyboardType="decimal-pad"
          />
        </View>
        <View style={styles.inputRow}>
          <TextInput
            style={[styles.input, styles.flex1]}
            placeholder={t.placeholderQty}
            placeholderTextColor="#999"
            value={itemQty}
            onChangeText={setItemQty}
            keyboardType="number-pad"
          />
          <ScrollView horizontal showsHorizontalScrollIndicator={false} style={styles.discountRow}>
            {DISCOUNT_OPTIONS.map((opt) => (
              <TouchableOpacity
                key={String(opt.value)}
                style={[
                  styles.discountChip,
                  itemDiscount === opt.value && styles.discountChipActive,
                ]}
                onPress={() => setItemDiscount(opt.value)}
              >
                <Text
                  style={
                    itemDiscount === opt.value ? styles.discountChipTextActive : styles.discountChipText
                  }
                >
                  {t[opt.labelKey]}
                </Text>
              </TouchableOpacity>
            ))}
          </ScrollView>
        </View>
        {showCustomDiscount && (
          <TextInput
            style={[styles.input, styles.customDiscountInput]}
            placeholder=": 85 (%)"
            placeholderTextColor="#999"
            value={customDiscount}
            onChangeText={setCustomDiscount}
            keyboardType="number-pad"
          />
        )}
        <TouchableOpacity style={styles.btnAdd} onPress={addItem}>
          <Text style={styles.btnAddText}>{t.addBtn}</Text>
        </TouchableOpacity>
      </View>

      <View style={styles.card}>
        <View style={styles.listHeader}>
          <Text style={styles.listTitle}>{t.listTitle}</Text>
          <View style={styles.totalBlock}>
            <Text style={styles.totalLabel}>
              {t.totalLabel}: {symbol}{total.toLocaleString()}
            </Text>
            {showConvert && (
              <Text style={styles.totalTwd}>
                 {convertSymbol} {Math.round(total * convertRate).toLocaleString()}
              </Text>
            )}
          </View>
        </View>
        {items.map((i) => (
          <View key={i.id} style={styles.listItem}>
            <TouchableOpacity
              style={styles.itemMain}
              onPress={() => editItem(i.id)}
              activeOpacity={0.7}
            >
              <Text style={styles.itemName}>
                {i.name} x{i.qty}
              </Text>
              <Text style={styles.itemPrice}>{symbol}{(i.singleFinalPrice * i.qty).toLocaleString()}</Text>
            </TouchableOpacity>
            <View style={styles.itemDetail}>
              <Text style={styles.itemDetailText}>
                 {symbol}{i.singleFinalPrice}
                {i.discount < 1 ? ` (${Math.round(i.discount * 100)}%)` : ''}
              </Text>
              <TouchableOpacity onPress={() => deleteItem(i.id)} style={styles.btnDel}>
                <IconTrash color={colors.danger} size={20} />
              </TouchableOpacity>
            </View>
          </View>
        ))}
        <View style={styles.calcRow}>
          {target > 0 && (
            <TouchableOpacity style={styles.btnCalc} onPress={calculateGroups}>
              <Text style={styles.btnCalcText}>{t.calcBtn}</Text>
            </TouchableOpacity>
          )}
          <TouchableOpacity
            style={styles.btnSave}
            onPress={openSaveModal}
            disabled={items.length === 0}
          >
            <Text style={[styles.btnSaveText, items.length === 0 && styles.btnSaveTextDisabled]}>
              {t.saveBtn}
            </Text>
          </TouchableOpacity>
        </View>
        <TouchableOpacity onPress={clearAll} style={styles.clearBtn}>
          <Text style={styles.clearBtnText}>{t.clearBtn}</Text>
        </TouchableOpacity>
      </View>

      {results.length > 0 && (
        <View style={styles.resultsSection}>
          <Text style={styles.resultsTitle}>{t.calcBtn}</Text>
          {results.map((g, idx) => {
            const ok = g.sum >= target;
            const summary = g.items.reduce<Record<string, number>>((acc, curr) => {
              acc[curr.name] = (acc[curr.name] || 0) + 1;
              return acc;
            }, {});
            const summaryText = Object.entries(summary)
              .map(([name, count]) => `${name} x${count}`)
              .join(' + ');
            return (
              <View
                key={idx}
                style={[styles.resultGroup, ok ? styles.resultGroupOk : styles.resultGroupFail]}
              >
                <View style={[styles.badge, ok ? styles.badgeOk : styles.badgeFail]}>
                  <Text style={[styles.badgeText, ok ? styles.badgeTextOk : styles.badgeTextFail]}>
                    {ok ? t.qualified : t.unqualified}
                  </Text>
                </View>
                <Text style={styles.resultSum}>
                  {t.groupName.replace('{n}', String(idx + 1))}: {symbol}{g.sum.toLocaleString()}
                </Text>
                {showConvert && (
                  <Text style={styles.resultTwd}>
                     {convertSymbol} {Math.round(g.sum * convertRate)}
                  </Text>
                )}
                <Text style={styles.resultSummary}>{summaryText}</Text>
                {!ok && (
                  <Text style={styles.suggestion}>
                    {t.suggest.replace('{m}', String(target - g.sum)).replace('', symbol)}
                  </Text>
                )}
              </View>
            );
          })}
        </View>
      )}

      <Modal
        visible={saveModalVisible}
        transparent
        animationType="fade"
        onRequestClose={() => setSaveModalVisible(false)}
      >
        <TouchableOpacity
          style={styles.modalOverlay}
          activeOpacity={1}
          onPress={() => setSaveModalVisible(false)}
        >
          <View style={styles.modalBox} onStartShouldSetResponder={() => true}>
            <Text style={styles.modalTitle}>{t.saveModalTitle}</Text>
            <TextInput
              style={styles.modalInput}
              placeholder={t.saveModalPlaceholder}
              placeholderTextColor="#999"
              value={saveModalName}
              onChangeText={setSaveModalName}
              autoFocus
            />
            <View style={styles.modalButtons}>
              <TouchableOpacity
                style={styles.modalBtnCancel}
                onPress={() => setSaveModalVisible(false)}
              >
                <Text style={styles.modalBtnCancelText}>{t.saveModalCancel}</Text>
              </TouchableOpacity>
              <TouchableOpacity style={styles.modalBtnConfirm} onPress={saveCurrentList}>
                <Text style={styles.modalBtnConfirmText}>{t.saveModalConfirm}</Text>
              </TouchableOpacity>
            </View>
          </View>
        </TouchableOpacity>
      </Modal>
    </ScrollView>
  );
}

