import React, { useCallback } from 'react';
import { Alert, ScrollView, StyleSheet, Text, TouchableOpacity, View } from 'react-native';
import type { Currency, ConvertTo } from './currency';
import { CURRENCIES } from './currency';
import type { Lang } from './i18n/visaOptimizer';
import { visaOptimizerI18n } from './i18n/visaOptimizer';

const CURRENCY_LABEL_KEYS: Record<Currency, keyof typeof visaOptimizerI18n.zh> = {
  JPY: 'currencyJPY',
  TWD: 'currencyTWD',
  USD: 'currencyUSD',
};

interface SettingsScreenProps {
  lang: Lang;
  baseCurrency: Currency;
  convertCurrency: ConvertTo;
  onBaseCurrencyChange: (c: Currency) => void;
  onConvertCurrencyChange: (c: ConvertTo) => void;
  onOpenLangPicker: () => void;
}

export function SettingsScreen({
  lang,
  baseCurrency,
  convertCurrency,
  onBaseCurrencyChange,
  onConvertCurrencyChange,
  onOpenLangPicker,
}: SettingsScreenProps) {
  const t = visaOptimizerI18n[lang];

  const openBaseCurrencyPicker = useCallback(() => {
    const buttons: Array<{ text: string; onPress?: () => void; style?: 'cancel' }> = [
      ...CURRENCIES.map((c) => ({
        text: t[CURRENCY_LABEL_KEYS[c]],
        onPress: () => onBaseCurrencyChange(c),
      })),
      { text: t.saveModalCancel, style: 'cancel' },
    ];
    Alert.alert(t.settingsBaseCurrency, undefined, buttons);
  }, [lang, onBaseCurrencyChange, t]);

  const openConvertCurrencyPicker = useCallback(() => {
    const buttons: Array<{ text: string; onPress?: () => void; style?: 'cancel' }> = [
      { text: t.currencyNone, onPress: () => onConvertCurrencyChange('none') },
      ...CURRENCIES.filter((c) => c !== baseCurrency).map((c) => ({
        text: t[CURRENCY_LABEL_KEYS[c]],
        onPress: () => onConvertCurrencyChange(c),
      })),
      { text: t.saveModalCancel, style: 'cancel' },
    ];
    Alert.alert(t.settingsConvertCurrency, undefined, buttons);
  }, [lang, baseCurrency, onConvertCurrencyChange, t]);

  const baseLabel = t[CURRENCY_LABEL_KEYS[baseCurrency]];
  const convertLabel =
    convertCurrency === 'none' ? t.currencyNone : t[CURRENCY_LABEL_KEYS[convertCurrency]];

  return (
    <ScrollView
      style={styles.scroll}
      contentContainerStyle={styles.scrollContent}
      keyboardShouldPersistTaps="handled"
    >
      <Text style={styles.sectionTitle}>{t.settingsTitle}</Text>

      <TouchableOpacity style={styles.card} onPress={onOpenLangPicker} activeOpacity={0.7}>
        <View style={styles.langRow}>
          <Text style={styles.label}>{t.settingsLang}</Text>
          <View style={styles.langValueRow}>
            <Text style={styles.langValue}>{t.settingsLangValue}</Text>
            <Text style={styles.chevron}></Text>
          </View>
        </View>
      </TouchableOpacity>

      <TouchableOpacity style={styles.card} onPress={openBaseCurrencyPicker} activeOpacity={0.7}>
        <View style={styles.langRow}>
          <Text style={styles.label}>{t.settingsBaseCurrency}</Text>
          <View style={styles.langValueRow}>
            <Text style={styles.langValue}>{baseLabel}</Text>
            <Text style={styles.chevron}></Text>
          </View>
        </View>
      </TouchableOpacity>

      <TouchableOpacity style={styles.card} onPress={openConvertCurrencyPicker} activeOpacity={0.7}>
        <View style={styles.langRow}>
          <Text style={styles.label}>{t.settingsConvertCurrency}</Text>
          <View style={styles.langValueRow}>
            <Text style={styles.langValue}>{convertLabel}</Text>
            <Text style={styles.chevron}></Text>
          </View>
        </View>
      </TouchableOpacity>
    </ScrollView>
  );
}

const colors = {
  visaBlue: '#0033a0',
  bg: '#f0f2f5',
  card: '#fff',
  text: '#333',
};

const styles = StyleSheet.create({
  scroll: { flex: 1, backgroundColor: colors.bg },
  scrollContent: { padding: 10, paddingBottom: 24 },
  sectionTitle: {
    fontSize: 18,
    fontWeight: '600',
    color: colors.visaBlue,
    marginBottom: 12,
  },
  card: {
    backgroundColor: colors.card,
    padding: 14,
    borderRadius: 12,
    marginBottom: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.05,
    shadowRadius: 6,
    elevation: 2,
  },
  label: { fontSize: 14, fontWeight: '600', color: colors.text },
  langRow: { flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between' },
  langValueRow: { flexDirection: 'row', alignItems: 'center', gap: 4 },
  langValue: { fontSize: 14, color: colors.text },
  chevron: { fontSize: 18, color: '#999' },
});
